import React from 'react';
import MediaQuery from 'react-responsive';

//General style variables
const styleHeader = {
    boxSizing: "border-box",
    minHeight: "100vh",
    padding: "10vh 0 0",
    position: "relative",
    transformStyle: "inherit",
    width: "100vw",
    textAlign: "center",
    color: "white",
    textShadow: "-1.5px 0 black, 0 1.5px black, 1.5px 0 black, 0 -1.5px black"
}

//Desktop style variables
const styleImageDesktop = {
    width: "12vw",
    padding: 0,
    background: "white",
    borderRadius: "100%",
    border: "1px solid black"
}

const styleTitleDesktop ={
    fontSize: "5vw"
}

//Laptop style variables
const styleImageLaptop = {
    width: "17vw",
    padding: 0,
    background: "white",
    borderRadius: "100%",
    border: "1px solid black"
}

const styleTitleLaptop ={
    fontSize: "5vw",
    padding: "7vh"
}

//Mobile style variables
const styleImageMobile = {
    width: "30vw",
    padding: 0,
    marginLeft: "55%",
    background: "white",
    borderRadius: "100%",
    border: "1.5px solid black"
}

const styleTitleMobile ={
    fontSize: "8vw",
    padding: "3vh",
    textShadow: "-2px 0 black, 0 2px black, 2px 0 black, 0 -2px black"
}

export class Header extends React.Component {
    render () {
        return (
            <div style={styleHeader} className="header courgette" id="header" >
                <MediaQuery minWidth={1400} >
                    <h1 style={styleTitleDesktop} >Crowns Zita Cardin</h1>
                    <img style={styleImageDesktop} src="https://raw.githubusercontent.com/anthony6310/cardin/master/Website/Logo1.png" alt=""/>
                </MediaQuery>  
                <MediaQuery minWidth={768} maxWidth={1400} >
                    <h1 style={styleTitleLaptop} >Crowns Zita Cardin</h1>
                    <img style={styleImageLaptop} src="https://raw.githubusercontent.com/anthony6310/cardin/master/Website/Logo1.png" alt=""/>
                </MediaQuery>  
                <MediaQuery maxWidth={768} >
                    <h1 style={styleTitleMobile} >Crowns Zita Cardin</h1>
                    <img style={styleImageMobile} src="https://raw.githubusercontent.com/anthony6310/cardin/master/Website/Logo1.png" alt=""/>
                </MediaQuery>    
            </div>
        );
    }
}