import React from 'react';
import { GridImage } from './GridImage';
import MediaQuery from 'react-responsive';

const styleGridDesktop = {
  margin: "0 auto",
  marginTop: 20,
  display: 'grid',
  gridTemplateColumns: "auto auto auto",
  gridTemplateRows: "auto auto auto",
  gridGap: 10,
  padding: 0,
  width: "65%"
};

const styleGridLaptop = {
  margin: "0 auto",
  marginTop: 20,
  display: 'grid',
  gridTemplateColumns: "auto auto auto",
  gridTemplateRows: "auto auto auto",
  gridGap: 10,
  padding: 0,
  width: "80%"
};

const styleGridMobile = {
  margin: "0 auto",
  marginTop: 20,
  display: 'grid',
  gridTemplateColumns: "auto auto auto",
  gridTemplateRows: "auto auto auto",
  gridGap: 10,
  padding: 0,
  width: "90%"
}

const styleTitleDesktop = {
  fontSize: "3vw",
  marginTop: 25
};

const styleTitleLaptop = {
  fontSize: "5vw",
  marginTop: 25
};

const styleTitleMobile = {
  fontSize: "10vw",
  marginTop: 25
};

const styleDescriptionDesktop = {
  fontSize: "2vw"
};

const styleDescriptionLaptop = {
  fontSize: "3vw"
};

const styleDescriptionMobile = {
  fontSize: "5vw"
};

const styleContainer = {
  textAlign: "center"
};

export class Grid extends React.Component {

  constructor (props) {
    super(props);
    this.state = {
      zoom: false,
      index: -1
    };
    this.zoomImage = this.zoomImage.bind(this);
  }

  zoomImage (index) {
    this.setState({
      index: index,
      zoom: !this.state.zoom
    });
  }

  render() {
    return (
      <div className="container" style={styleContainer}>
        <MediaQuery minWidth={1400} >
          <div className="courgette" style={styleTitleDesktop}><span id={this.props.title}>{this.props.title}</span></div>
          <div className="yanone" style={styleDescriptionDesktop}><span>{this.props.description}</span></div>
          <div style={styleGridDesktop}>
            <GridImage onClick={this.zoomImage} source={this.props.images[0]} index={0} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[1]} index={1} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[2]} index={2} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[3]} index={3} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[4]} index={4} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[5]} index={5} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[6]} index={6} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[7]} index={7} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[8]} index={8} zoomIndex={this.state.index} zoomState={this.state.zoom} />
          </div>
        </MediaQuery>
        <MediaQuery minWidth={768} maxWidth={1400} >
          <div className="courgette" style={styleTitleLaptop}><span id={this.props.title}>{this.props.title}</span></div>
          <div className="yanone" style={styleDescriptionLaptop}><span>{this.props.description}</span></div>
          <div style={styleGridLaptop}>
            <GridImage onClick={this.zoomImage} source={this.props.images[0]} index={0} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[1]} index={1} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[2]} index={2} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[3]} index={3} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[4]} index={4} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[5]} index={5} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[6]} index={6} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[7]} index={7} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[8]} index={8} zoomIndex={this.state.index} zoomState={this.state.zoom} />
          </div>
        </MediaQuery>
        <MediaQuery maxWidth={768}>
          <div className="courgette" style={styleTitleMobile}><span id={this.props.title}>{this.props.title}</span></div>
          <div className="yanone" style={styleDescriptionMobile}><span>{this.props.description}</span></div>
          <div style={styleGridMobile}>
            <GridImage onClick={this.zoomImage} source={this.props.images[0]} index={0} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[1]} index={1} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[2]} index={2} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[3]} index={3} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[4]} index={4} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[5]} index={5} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[6]} index={6} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[7]} index={7} zoomIndex={this.state.index} zoomState={this.state.zoom} />
            <GridImage onClick={this.zoomImage} source={this.props.images[8]} index={8} zoomIndex={this.state.index} zoomState={this.state.zoom} />
          </div>
        </MediaQuery>
      </div>
    );
  }
}