import React from 'react';
import MediaQuery from 'react-responsive';

const styleWidget = {
    border: 0,
    overflow: "hidden",
    height: "100%",
    width: "100%",
    padding: "2vw",
    borderRadius: "15%",
    background: "white"    
}

const styleWidgetMobile = {
    border: 0,
    overflow: "hidden",
    height: "100%",
    width: "100%",
    padding: "4vw",
    borderRadius: "15%",
    background: "white"    
}

const styleContainer = {
    textAlign: "center"
}

const styleTitleDesktop = {
    fontSize: "2.5vw"
}

const styleTitleLaptop = {
    fontSize: "5vw"
}

const styleTitleMobile = {
    fontSize: "10vw"
}

const styleFeedBorderDesktop = {
    width: "30%",
    height: "30vw",
    padding: "1.5vw",
    borderRadius: "20%",
    background: "rgb(252,176,69)",
    background: "linear-gradient(318deg, rgba(252,176,69,1) 0%, rgba(253,29,29,1) 50%, rgba(131,58,180,1) 100%)",
    display: "inline-block"
}

const styleFeedBorderLaptop = {
    width: "40%",
    height: "40vw",
    padding: "2vw",
    borderRadius: "20%",
    background: "rgb(252,176,69)",
    background: "linear-gradient(318deg, rgba(252,176,69,1) 0%, rgba(253,29,29,1) 50%, rgba(131,58,180,1) 100%)",
    display: "inline-block"
}

const styleFeedBorderMobile = {
    width: "90%",
    height: "90vw",
    padding: "4vw",
    borderRadius: "20%",
    background: "rgb(252,176,69)",
    background: "linear-gradient(318deg, rgba(252,176,69,1) 0%, rgba(253,29,29,1) 50%, rgba(131,58,180,1) 100%)",
    display: "inline-block"
}

export class Instagram extends React.Component {
    render () {
        return (
            <div style={styleContainer} >
                <MediaQuery minWidth={1400}>
                    <div className="courgette" style={styleTitleDesktop}>
                        <span id={this.props.title}>{this.props.title}</span>
                    </div>
                    <div style={styleFeedBorderDesktop} >
                        <iframe style={styleWidget}
                        src="https://cdn.lightwidget.com/widgets/fd5f7d72ad4c53b385a56aebab7b905e.html" 
                        scrolling="no"></iframe>
                    </div>
                </MediaQuery>
                <MediaQuery minWidth={768} maxWidth={1400}>
                    <div className="courgette" style={styleTitleLaptop}>
                        <span id={this.props.title}>{this.props.title}</span>
                    </div>
                    <div style={styleFeedBorderLaptop} >
                        <iframe style={styleWidget}
                        src="https://cdn.lightwidget.com/widgets/fd5f7d72ad4c53b385a56aebab7b905e.html" 
                        scrolling="no"></iframe>
                    </div>
                </MediaQuery>
                <MediaQuery maxWidth={768}>
                    <div className="courgette" style={styleTitleMobile}>
                        <span id={this.props.title}>{this.props.title}</span>
                    </div>
                    <div style={styleFeedBorderMobile} >
                        <iframe style={styleWidgetMobile}
                        src="https://cdn.lightwidget.com/widgets/fd5f7d72ad4c53b385a56aebab7b905e.html" 
                        scrolling="no"></iframe>
                    </div>
                </MediaQuery>
            </div>
        );
    }
}